<template>
  <div class="center requirement">
    <div class="center_breadcrumb">
         <crumb v-if="isOperate ==='taskSituation'" :data="pageTitle"></crumb>
         <crumb v-else></crumb>
    </div>
    <div class="center_content" style="margin-bottom: 110px;overflow: hidden;">
        <div class="requirement-side">
            <h3>毕业要求</h3>
            <ul>
                <li :class="{ active: isActive == index  }"  v-for="(item, index) in datalist" :key="index" @click="getPointCourse(item.id,item,index)">
                    <span style="overflow: hidden;white-space: nowrap;text-overflow: ellipsis;">
                        毕业要求{{index+1}}:{{item.name}}
                    </span>
                </li>
            </ul>
        </div>
        <div class="requirement-main">
            <keep-alive>
                <div class="requirement-title"  v-if="datalist.length == 0 ? false:true">
                    <label>毕业要求{{Number(isActive) + 1}}:&nbsp;&nbsp;</label><span>{{name}}：{{content}}</span>
                </div>
            </keep-alive>
        
            <div class="requirement-item">
                <ul class="requirement-item_ul">
                    <li v-for="(item, index) in courseList" :key="index" class="requirement-item_li">
                         <div class="item-lf">{{item.code}}</div>
                         <div class="item-rt">
                            <p>指标点{{item.code}}</p>
                            <span>{{item.content}}</span>
                            <ul>
                                <li v-for="(items, index) in item.courseList" :key="index" @click="jump(items.courseId)">
                                   {{items.courseName}}
                                </li>
                            </ul>
                         </div>
                    </li>
                </ul>
            </div>
        </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
        pageTitle:['培养方案内容', '毕业要求'],
        isOperate:this.$route.query.type, //教学任务管理-任务完成情况跳转过来携带 type:taskSituation无操作权限
        datalist:[],
        courseList:[],
        name:'',
        content:'',
        id:0,
        index:'',
        isActive:0,
        userIdentity: '',
        versionList: [
        {
            "name": "2016",
            "id": "580700181184380928",
            "minimum": 0.7,
            "specialty_id": "575743169551925248",
            "graduation_year": "2020",
            "grade": "2016"
        }
   ],
        versionId: 0,
        roleName: ''
    };
    },
    methods: {
        getClaim(id) {
            this.versionId = id
            this.$get('/info/teacherinfo/getGraduationClaimByVersionId', {versionId: this.versionId}).then(res => {
                this.datalist = res
                if(this.datalist.length > 0) {
                this.$get('/info/information/getPointCourse?graduationId='+this.datalist[0].id).then(res=>{
                    this.courseList = res
                    if(this.datalist.length > 0) {
                        this.getPointCourse(this.datalist[0].id,this.datalist[0],0)
                    }
                })
                }
            })
        },
        jump(id){
            this.$router.push({path: '/studentside/graduation',query: {id: id, title:"毕业要求"}})
        },
        // /information/information/getGraduationClaim
        getGraduationClaim(){  //学生端
            this.$get('/info/information/getGraduationClaim').then(res=>{
            
                this.datalist = res
                if(this.datalist.length > 0) {
                this.getPointCourse(this.datalist[0].id,this.datalist[0],0)
                }
            })
        },
        // /info/information/getPointCourse   学生端
        getPointCourse(id,row,index){
            this.name = row.name
            this.isActive = index
            this.content = row.content
            this.id = id
            this.index = index
            localStorage.setItem("index",index)
            localStorage.setItem('id',id)
            localStorage.setItem('content',row.content)
            localStorage.setItem('name',row.name)
            if(localStorage.getItem("index")){
                this.isActive = localStorage.getItem("index")
            }
            this.$get('/info/information/getPointCourse?graduationId='+id).then(res=>{
                this.courseList = res
            })
        },
        getItem(){
            if(localStorage.getItem("index")){
                this.isActive = localStorage.getItem("index")
            }else{
                this.isActive = 0
            }
            if(localStorage.getItem('id')){
                this.$get('/info/information/getPointCourse?graduationId='+localStorage.getItem('id')).then(res=>{
                    this.courseList = res
                })
            }else{

            }
            if(localStorage.getItem('content')){
                this.content = localStorage.getItem('content')
            }
            if(localStorage.getItem('name')){
                this.name = localStorage.getItem('name')
            }
            
        },
        getInfo() {
            if(localStorage.getItem('versionId')) {
                this.getClaim(localStorage.getItem('versionId'))
            } else {
                setTimeout(() => {
                this.getInfo()
                }, 200); 
            }
        }
    },
    created() {
        this.roleName = localStorage.getItem('roleName')
    },
    mounted(){

    
    if(localStorage.getItem('roleName') === '课程负责人' || localStorage.getItem('roleName') === '教师') {
        this.getInfo()
    } else if(localStorage.getItem('roleName') === '学生') {
        this.getGraduationClaim()
    }

    // if(this.userIdentity == 'teacher') {
    //   this.getAllVersion()
    // } else {
    //   this.getGraduationClaim()
    // }
    this.$nextTick(()=>{
        this.getItem()
    })
  },
};
</script>
<style lang="scss">
@import "../../styles/center.scss";
.requirement {
    .requirement-side{
        width:260px;
        min-height:100%;
        overflow-y: auto;
        background:#fff;
        border-radius:10px;
        float:left;
        margin-bottom:80px;
        h3{
               line-height:80px;
               text-align:center;
                color:#323232;
                font-size:20px;
            }
        ul{
            li{
                list-style: none;
                font-size: 14px;
                overflow: hidden; 
                cursor: pointer;
                background:#fff;
                border-radius:20px;
                color:#696969;
                height: 60px;
                width: 100%;
                position: relative;
                span{
                    display: inline-block;
                    width: 170px;
                    height: 40px;
                    font-size: 14px;
                    line-height: 40px;
                    text-align: center;
                    background: #fff;
                    color: #696969;
                    border-radius: 20px;
                    white-space: nowrap;
                    overflow: hidden;
                    text-overflow: ellipsis;
                    padding: 0 20px;
                    position: absolute;
                    left: 0;
                    top: 0;
                    right: 0;
                    bottom: 0;
                    margin: auto;
                }
                 &.active{
                        span{
                            background:#03B187;
                            box-shadow:0px 2px 20px 0px rgba(3,177,135,0.3);
                            color:#fff;
                            font-size:16px;
                            
                        }    
                    }
            }
        }
    }
    .requirement-main{
        margin-left: 20px;
        text-align: left;
        width: calc(100% - 320px);
        overflow-y: auto;
        float: left;
        background: #fff;
        border-radius:10px;
        padding: 20px;
        .requirement-title{
            margin-top: 20px;
            margin-bottom: 20px;
            margin-left: 22px;
            color:#323232;
            font-weight:bold;
            label{
              font-size: 20px;
            }
            span{ 
              font-size: 14px;
            }
        }
    }
    .requirement-item{
        width: 100%;
        .requirement-item_ul{
            .requirement-item_li{
               min-height:92px;
                width: 100%;
                text-align:left;
                display:flex;
                justify-content: space-between;
                padding-bottom: 20px;
                .item-lf{
                    height: 49px;
                    width: 49px;
                    border: 1px solid #cccccc;
                    border-radius: 50%;
                    position: relative;
                    display: -webkit-box;
                    display: -ms-flexbox;
                    display: flex;
                    -webkit-box-pack: center;
                    -ms-flex-pack: center;
                    justify-content: center;
                    -webkit-box-align: center;
                    -ms-flex-align: center;
                    align-items: center;
                    color: #696969;
                    font-size: 16px;
                }
                .item-lf:after{
                    content:'';
                    width:14px;
                    height:14px;
                    background:rgba(3,177,135,1);
                    border-radius:50%;
                    position:absolute;
                    left:0;
                    top:0;
                 }
                 .item-rt{
                    width:calc(100% - 80px);
                    p{
                            font-size:14px;
                            color:#323232;
                    }
                    span{
                        display: inline-block;
                            font-size:14px;
                            color:#323232;
                            margin:10px 0;
                    }
                    ul{
                        li{
                            padding:3px 8px;
                            border:1px solid rgba(221,221,221,1);
                            border-radius:5px;
                            font-size:14px;
                            color:#969696;
                            float:left;
                            margin-right:20px;
                            margin-bottom:10px;
                            cursor:pointer;
                            &:hover{
                                color:#03B187;
                                font-weight:bold;
                                background:rgba(3,177,135,0.1);
                                border-color:rgba(3,177,135,1);

                            }
                        }
                    }
                 }
            }
        }
    }
}
</style>



